import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { DATES, InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../../dependencies/empresaMLFDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { ORIGEM_LANCAMENTO_MLF_ENUM_VALUES } from '../../../../../models/enumerated/mlf/origemLancamentoMLFEnum';
import { ANALITICO, SINTETICO_ANALITICO_ENUM_VALUES } from '../../../../../models/enumerated/mlf/sinteticoAnaliticoEnum';
import { SAIDA } from '../../../../../models/enumerated/mlf/tipoMovimentoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, EntradaSaidaCidadeRV, REPORT_EVENT } from './entradaSaidaCidadeRV';

const DEFAULT_VALUES: EntradaSaidaCidadeRV = {
  ...getDefaultReportBasicValues('Relação de Saídas por Cidade'),
  gerarCabecalhoPadraoAdministradora: true,
  origemLancamento: null,
  sinteticoAnalitico: ANALITICO.value,
  tipoMovimento: SAIDA.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  exibirConfiguracaoDadosEmpresa: false,
  exibirConfiguracaoMultiplasEmpresas: true,
};

const SaidaCidadeReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<EntradaSaidaCidadeRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Relação de Saídas por Cidade"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
      wikiId={4320}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <InputGroupInline formProps={formProps} id="tipoMovimento" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              id="sinteticoAnalitico"
              label="Modelo de Relatório"
              isPrependLabel={true}
              placeholder="Informe o Modelo de Relatório"
              formProps={formProps}
              inputDropDownProps={{
                optionsProvider: SINTETICO_ANALITICO_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="origemLancamento"
              label="Origem do Lançamento"
              isPrependLabel={true}
              placeholder="Informe a Origem do Lançamento"
              formProps={formProps}
              inputDropDownProps={{
                optionsProvider: ORIGEM_LANCAMENTO_MLF_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default SaidaCidadeReportView;
